import React, { useEffect } from 'react'

import { ServiceFlex } from '../../styles/servicesStyles'
import { Container, BlackContainer } from '../../styles/globalStyles'

// import ServiceCard from '../cards/serviceCard'
import ServicesCard from '../cards/servicesCard'
// import ServiceVideoCard from '../cards/serviceVideoCards'
import HomeTitle from '../homepage/homeTitle'

import websitesAppsImage from '../../assets/images/illustrations/webdev.svg'
import uiuxImage from '../../assets/images/illustrations/uiux.svg'
import graphicDesignImage from '../../assets/images/illustrations/graphicdesign.svg'

import vidIX from '../../assets/video/services.mp4';

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'
import VideoCaption from '../videoCaption/videoCaption'


const cardDescriptions = [
    {   id: 0, 
        title: "Websites e apps.", 
        desc: "Quer mostrar o potêncial da sua empresa ou criar um portfólio pessoal? Faremos as suas ideias, uma realidade. Desenhamos e criamos websites e apps adaptáveis a qualquer tipo de dispositivos.",
        // video: vidWA,
        image: websitesAppsImage
    },
    {   id: 1, 
        title: "Design Gráfico.", 
        desc: "Acreditamos que ter uma identidade forte e reconhecível é a chave para um negócio com sucesso. Assim, ajudamos pessoas e empresas a tomar decisões corretas, um passo de cada vez.",
        // video: vidDG,
        image: graphicDesignImage
    },
    {   id: 2, 
        title: "UI/UX Design", 
        desc: "Encontramos o melhor design para o vosso website ou aplicação móvel. A interção com o utilizador é sempre prioridade, e é com esse pensamento que desenhamos todos os nossos projetos.",
        // video: vidIX,
        image: uiuxImage
    }

]

    

const ServicesVideo = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <>
            <HomeTitle title="torne ideias realidade." leading="" />
            <VideoCaption video={vidIX} caption ="O que fazemos?" desc="Acreditamos que ter uma identidade forte e reconhecível é a chave para um negócio com sucesso. Assim, ajudamos pessoas e empresas a tomar decisões corretas, um passo de cada vez." /> 
            <BlackContainer>

                <Container>
                    <ServiceFlex>
                        {cardDescriptions.map(route => (
                            <ServicesCard key={route.id}
                                title={route.title}
                                desc={route.desc}
                                image={route.image} 
                                >    
                            </ServicesCard> 
                        ))}
                    </ServiceFlex>
                    
                </Container>
                             
            </BlackContainer>
        </>
        
    )
}

export default ServicesVideo;